<template>
  <v-container>
    <v-row>
      <v-col cols="6">
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">Skill</th>
                <th class="text-left">Type</th>
                <th class="text-left">Level</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="item in skills" :key="item.name">
                <td>
                  <img :src="item.img" class="small" />
                  {{ item.name }}
                </td>
                <td>{{ item.type }}</td>
                <td>
                  <v-text-field
                    v-model="levels[item.id]"
                    dense
                    type="number"
                    full-width
                    style="height: 45px; width: 40px;"
                    va
                  ></v-text-field>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-col>
      <v-col cols="6">
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">Currency</th>
                <th class="text-left">Amount</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="currency in currencies" :key="currency.id">
                <td>{{ currency.name }} ({{currency.symbol}})</td>
                <td>
                  <v-text-field
                    v-model="money[currency.id]"
                    dense
                    type="number"
                    full-width
                    style="height: 45px;"
                    va
                  ></v-text-field>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <br />
        <v-simple-table dense>
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">Trader</th>
                <th class="text-left">Loyalty</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="trader in traders" :key="trader.id">
                <td>
                  <img :src="trader.img" class="small" />
                  {{ trader.name }}
                </td>
                <td>
                  <v-text-field
                    v-model="loyalties[trader.id]"
                    dense
                    type="number"
                    full-width
                    style="height: 45px;"
                    va
                  ></v-text-field>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Character",

  data: () => ({
    skills: [],
    levels: {},
    currencies: [],
    money: {},
    traders: [],
    loyalties: {}
  }),
  methods: {
    increase: function(id) {
      this.$set(this.levels, id, parseInt(this.levels[id]) + 1);
    }
  },
  computed: {
    storeStats: function() {
      console.log("computed storeStats", this.$store.state.levels);
      return this.$store.state.levels;
    },
    storeMoney: function() {
      console.log("computed storeMoney", this.$store.state.money);
      return this.$store.state.money;
    },
    storeLoyalties: function() {
      console.log("computed storeLoyalties", this.$store.state.loyalties);
      return this.$store.state.loyalties;
    }
  },
  watch: {
    storeStats: function(newVal) {
      console.log("watch storeStats", newVal);
      this.levels = newVal;
    },
    storeMoney: function(newVal) {
      console.log("watch storeMoney", newVal);
      this.money = newVal;
    },
    storeLoyalties: function(newVal) {
      console.log("watch storeLoyalties", newVal);
      this.loyalties = newVal;
    },
    levels: {
      handler(newVal) {
        this.$store.commit("setLevels", newVal);
      },
      deep: true
    },
    money: {
      handler(newVal) {
        this.$store.commit("setMoney", newVal);
      },
      deep: true
    },
    loyalties: {
      handler(newVal) {
        this.$store.commit("setLoyalties", newVal);
      },
      deep: true
    }
  },
  created() {
    this.skills = this.staticData.skills;
    var levels =
      Object.keys(this.$store.state.levels).length > 0
        ? this.$store.state.levels
        : {
            level: 0,
            attention: 0,
            charisma: 0,
            endurance: 0,
            health: 0,
            immunity: 0,
            intellect: 0,
            memory: 0,
            metabolism: 0,
            perception: 0,
            strength: 0,
            stress_resistance: 0,
            vitality: 0
          };
    if (Object.keys(levels).length >= 0) {
      this.skills.forEach(s => this.$set(this.levels, s.id, levels[s.id]));
    }
    this.currencies = this.staticData.currencies;
    this.traders = this.staticData.traders;
    var money =
      Object.keys(this.$store.state.money).length > 0
        ? this.$store.state.money
        : {
            rouble: 0,
            euro: 0,
            dollar: 0,
            bitcoin: 0,
            gp_coin: 0
          };
    if (Object.keys(money).length >= 0) {
      this.currencies.forEach(s => this.$set(this.money, s.id, money[s.id]));
    }

    var loyalties =
      Object.keys(this.$store.state.loyalties).length > 0
        ? this.$store.state.loyalties
        : {
            prapor: 0,
            therapist: 0,
            fence: 0,
            skier: 0,
            peacekeeper: 0,
            mechanic: 0,
            ragman: 0,
            jaeger: 0
          };
    if (Object.keys(loyalties).length >= 0) {
      this.traders.forEach(t =>
        this.$set(this.loyalties, t.id, loyalties[t.id])
      );
    }
  }
};
</script>
<style>
img.small {
  max-width: 30px;
  max-height: 30px;
  vertical-align: middle;
  margin-right: 10px;
}
</style>
